html,
body,
.App {
  --primary-color: #110f2c;
  --text: #41c76e;
  --myFont: "Lucida Sans Typewriter", "Lucida Console", monaco,
    "Bitstream Vera Sans Mono", monospace;
  position: absolute;
  background: #000000;
  display: flex;
  flex-direction: row;
  color: var(--text);
  width: 100vw;
  height: 100vh;
  padding: 0px;
  margin: 0px;
}
body * {
  font-family: var(--myFont);
}
/*sidebar*/
.sidebar {
  position: relative;
  width: 300px;
  overflow: auto;
  background: var(--primary-color);
  border-right: 1px solid rgb(129, 128, 128);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 20px 0 20px;
}
.icon {
  margin-top: 70px;
}

/*searching options*/
.search {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 90%;
  height: auto;
  margin: 20px;
}
button {
  margin: 5px 10px;
  width: auto;
  height: 30px;
  border: 1px solid transparent;
  border-radius: 10px;
  color: white;
  background: #110f2c;
  text-decoration: none;
  text-align: center;
  transition: 400ms;
  font-size: 10px;
}
button:hover,
button.active {
  background-color: #262436;
  border: 1px solid white;
}
.search select {
  width: 50px;
  height: 25px;
  text-align: center;
}

/*Directed*/
.directed {
  display: flex;
  color: white;
  justify-content: flex-start;
  align-items: center;
}
.directed input {
  margin: 0 20px;
}
.panel {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 10px 20px;
}
.hidden {
  display: none;
}
.upload {
  width: auto;
  font-size: 12px;
}
.upload:hover {
  color: blue;
}
/* Selections */
.selection {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 90%;
  margin: 10px 0;
}
.selection input,
.selection button {
  width: 100%;
  margin: 8px 0;
  padding: 5px 0;
}
.path {
  width: 100%;
  margin: 8px 0;
}
.selection select {
  width: 117px;
}

/*Reset*/
.reset {
  width: 90%;
  background: var(--text) !important;
}
